.app {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 170vh;
  background-color: #21D4FD;
  background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
  background-size: cover;
  background-position: center;
}

form {
  background-color: white;
  padding: 20px 60px;
  margin: 20px;
  border-radius: 15px;
}

h1 {
  color: rgb(77, 1, 77);
  text-align: center;
}

button {
  width: 100%;
  height: 50px;
  padding: 10px;
  border: none;
  background-color: rebeccapurple;
  color: white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 30px;
}

img{
   height: 200px;
   width: 200px;
}

@media screen and (max-width: 800px) {
  img{
    height: 150px;
    width: 150px;
  }

  form {
    padding: 60px 40px;
  }
  .app{
    height: 160vh;
  }
}